.Page {
  text-align: center; }

.Page-logo {
  -webkit-animation: Page-logo-spin infinite 20s linear;
          animation: Page-logo-spin infinite 20s linear;
  height: 30px; }

.Page-header {
  background-color: #222;
  height: 20%;
  padding: 2%;
  color: white; }

.Page-content {
  height: 80%;
  padding: 2%; }

.Page-footer {
  background-color: #222;
  height: 20%;
  padding: 2%;
  color: white; }

@-webkit-keyframes Page-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes Page-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 20px;
  height: 20px;
  left: 20px;
  top: 20px; }

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: white; }

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px; }

/* Color/shape of close button cross */
.bm-cross {
  background: white; }

/* General sidebar styles */
.bm-menu {
  background: #222; }

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #222; }

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad; }

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3); }

a:-webkit-any-link {
  color: white;
  text-decoration: none; }

a:-moz-any-link {
  color: white;
  text-decoration: none; }

a:any-link {
  color: white;
  text-decoration: none; }



